/* create_project.css */

.modal {
    display: block;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    overflow: auto;
}

.modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.close {
    color: #aaa;
    float: right;
    font-size: 24px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: #555;
    text-decoration: none;
    cursor: pointer;
}

.project-form {
    display: flex;
    flex-direction: column;
}

.project-form label {
    margin-bottom: 10px;
    font-size: 16px;
}

.project-form input[type="text"] {
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
    outline: none;
    font-size: 16px;
}

.project-form button {
    padding: 12px 24px;
    margin-top: 10px;
    border: none;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    border-radius: 4px;
    outline: none;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.project-form button:hover {
    background-color: #0056b3;
}

.create-project {
    height: 100px;
}

.create-project-button {
    padding: 12px 24px;
    background-color: #a8406a;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    position: relative;
    left: calc(100% - 300px);
    top: 30px;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.create-project-button:hover {
    background-color: #aa6782;
}

/* create_project.css */
